import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {groupChannelsByCategories} from "eshop/modules/cart/analyzer/TvChannelUtils";

const UniversalProductDetails = (props) => {
    if(props.vas.tvChannelList && props.vas.tvChannelList.length > 0){
        return (<TvDetails {...props}/>);
    }
    return (<SimpleDetails {...props}/>);
}

const SimpleDetails = (props) => {
    return (<div className="g-gray1-bg u-padding-all-l u-no-padding-bottom u-text-left">
        <div className="l-row">
            <div className="l-col l-col-small-12 l-col-medium-3 l-col-3 "/>
            <div className="l-col l-col-small-12 l-col-medium-9 l-col-9"
                 dangerouslySetInnerHTML={{__html: props.vas.longDescription}}/>
        </div>
    </div>);
}

export const OfferAdapterForTvDetails = (props) => {
    let matchingTvPackage = props.tvPackages.find((tv) => props.productCode === tv.id);
    return <TvDetails vas={matchingTvPackage}/>
}

const TvDetails = (props) => {
    if (!props || !props.vas) {
        return null;
    }
    let groupedChannels = groupChannelsByCategories(props.vas.tvChannelList);
    return (<div className="g-gray1-bg u-padding-all-l u-no-padding-bottom u-text-left" style={{overflow:"hidden"}}>
        {groupedChannels && Object.keys(groupedChannels).length > 0 && Object.keys(groupedChannels).map((group) =>
            (<TvChannelCategory key={groupedChannels[group].label} group={groupedChannels[group]}/>)
        )}
    </div>);
}

const TvChannelCategory = (props) => {
    return (<div className="l-row">
        <div className="l-col l-col-small-12 l-col-medium-3 l-col-3 ">
            <p className="u-small-spacing-l">{props.group.label} :</p>
        </div>
        <div className="l-col l-col-small-12 l-col-medium-9 l-col-9 ">
            <ul className="opl-tv-channel-list opl-package-tv-choose">
                {props.group.channels.map((channel,index) => {
                    return (<TvChannelBox key={channel.code} channel={channel}/>);
                })}
            </ul>
        </div>
    </div>);
}

const TvChannelBox = (props) => {
    return (<li className="l-group__element opl-tv-channel-list__element opl-package-tv-choose__item u-margin-right">
        {props.channel.code.includes("HD") && (<span className="opl-package-tv-choose__hd">HD</span>)}
        <label className="o-checker__wrapper opl-checkbox opl-checker__wrapper">
            <input type="checkbox" name={props.channel.code} checked=""/>
            <span className="o-checker">
                        <span title={props.channel.code} lang="en">
                            <img src={!("empty" === props.channel.image) ? props.channel.image : "//:0"} alt={props.channel.name}/>
                        </span>
                    </span>
        </label>
    </li>)
}

export default UniversalProductDetails;

